@import url(https://fonts.googleapis.com/css?family=Italianno&display=swap);
@import url(https://fonts.googleapis.com/css?family=Inria+Serif&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
body, html, #root, .App {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%; }

.logo-wrapper {
  float: right; }
  .logo-wrapper h1 {
    padding-right: 8vw;
    padding-top: 9vh;
    font-family: 'Italianno', cursive;
    font-size: 50px;
    margin: 0; }
  @media screen and (max-width: 600px) {
    .logo-wrapper {
      float: none; }
      .logo-wrapper h1 {
        text-align: center;
        padding-top: 5vh;
        padding-right: 0;
        font-family: 'Italianno', cursive;
        font-size: 50px;
        margin: 0; } }

.project-list {
  padding-top: 42vh;
  padding-left: 7vw; }
  .project-list h1 {
    padding-left: 25px;
    margin: 0;
    font-family: 'Italianno', cursive;
    font-size: 70px;
    color: #e27d60; }
  .project-list ul {
    margin: 0;
    list-style-type: none;
    padding-left: 25px; }
  .project-list li {
    padding-bottom: 10px; }
    .project-list li:last-child {
      padding-bottom: 0; }
  .project-list a {
    text-decoration: none;
    color: #d9c4bf;
    font-family: 'Lobster', cursive;
    font-size: 25px; }
    .project-list a:visited {
      color: #d9c4bf; }
    .project-list a:hover {
      text-decoration: underline; }
  @media screen and (max-width: 600px) {
    .project-list {
      padding-left: 0; }
      .project-list h1 {
        padding: 0;
        font-family: 'Italianno', cursive;
        font-size: 50px;
        color: #e27d60;
        text-align: center; }
      .project-list ul {
        margin: 0;
        list-style-type: none;
        padding: 0;
        text-align: center; } }

.home-page {
  background-image: url(/personal-website/static/media/alexandre-godreau-_YEBZiTgZS8-unsplash.4957eb45.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  min-width: 100%;
  min-height: 100%; }

